
import { onMounted, ref } from "vue";
import axios from "axios";

export default {
  setup() {
    const replayList = ref({});
    onMounted(async () => {
      const response = await axios.post(`/api/fetchReplayOverview`, { user: "dummy" });
      replayList.value = JSON.parse(response.data).replayList;
    });
    return { replayList };
  },
};
